export default {
	data() {
		return {
			idx:'-1',
			list:[]
		};
	},
	props:{
		layoutList:{
			type:Array
		}
	},
	methods:{
		// 选中户型
		select(index,item){
			this.idx = index
			this.$emit('layout',this.idx,item)
		}
	},
	mounted(){
		this.$emit('layout',this.idx)
		if(this.list.length == 0){
			uni.showLoading({
			    title: '加载中'
			});
		}else{
			uni.hideLoading();
		}
		// 把户型中的数字拿出来
		setTimeout(()=>{
			this.list = this.layoutList;
			uni.setStorageSync('layoutList',this.layoutList)
			let list = this.list.map(item=>{
				return	{'houseType':item,'room':item.slice(0,item.indexOf('室')),'hall':item.slice(item.indexOf('室')+1,item.indexOf('卫'))}
					}
				);
			list.map(item=>{
				item.room = parseInt(item.room);
				item.hall = parseInt(item.hall);
			})
			this.list = list;
			uni.hideLoading();
		},200);
	}
}
